@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR|PT+Sans&display=swap');

html,
body {
    height: 100%;
    min-height: 100%;
    font-family: 'PT Sans', 'Noto Sans KR', sans-serif;
}
body {
    font-size: .875rem;
    overflow-y: scroll;
}

/*
* login
*/
.page-login {
    height: 100%;
    .container-fluid {
        &:before {
            height: 420px;
        }
    }
    .card {
        background-color: #f7fafc;
        &-body {
            padding: 50px;
        }
    }
    h1 {
        position: relative;
        text-align: center;
        text-transform: uppercase;
    }
    .input {
        &-group {
            position: relative;
            i {
                position: absolute;
                left: 0;
                top: 0;
                width: 50px;
                height: 100%;
                z-index: 4;
                text-align: center;
                line-height: 48px;
                opacity: .5;
            }
            .form-control {
                height: 48px;
                padding-left: 50px;
                font-size: 14px;
                box-shadow: 0 1px 3px rgba(50,50,93,.15), 0 1px 0 rgba(0,0,0,.02);
                border: 0;
                transition: box-shadow .15s ease;
                &:focus {
                    box-shadow: 0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08);
                     ~ i {
                         opacity: 1;
                     }
                }
            }
        }
    }
    .btn {
        height: 48px;
        font-size: 14px;
    }
}

/*
* header
*/
.header {
    // position: fixed;
    // top: 0;
    // right: 0;
    // left: 0;
    // z-index: 1030;
    position: relative;
    z-index: 1;
    height: 80px;
    background: linear-gradient(87deg,#172b4d,#1a174d) ;
}
.navbar {
    justify-content: flex-end;
    height: 100%;
    padding: 0;
    .nav {
        align-items: center;
        &-item {
            padding: 8px;
            .dropdown-item {
                font-size: 13px;
            }
        }
    }
}
.navbar-brand {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 101;
    display: block;
    width: 250px;
    height: 80px;
    margin: 0;
    padding: 0;
    text-align: center;
    line-height: 80px;
    background-color: #fff;
}

/*
* Container
*/
.container-fluid {
    height: 100%;
    padding: 0;
    box-sizing: border-box;
    background: #f8f9fe;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 320px;
        background: linear-gradient(87deg,#172b4d,#1a174d) ;
    }
}


/*
* Sidebar
*/
.sidebar {
    position: fixed;
    bottom: 0;
    left: 0;
    top: 80px;
    float: left;
    width: 250px;
    z-index: 100; /* Behind the navbar */
    box-shadow: 0 0 2rem 0 rgba(136,152,170,.15);
    background: #fff;
    &-sticky {
        position: relative;
        top: 0;
        height: calc(100vh - 50px);
        padding-top: .5rem;
        overflow-x: hidden;
        overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */

        @supports ((position: -webkit-sticky) or (position: sticky)) {
            position: -webkit-sticky;
            position: sticky;
        }
    }
    .nav-link {
        position: relative;
        padding: 10px 24px;
        font-weight: 500;
        color: rgba(0,0,0,.5);
        &:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            width: 2px;
            background-color: transparent;
        }
        i {
            min-width: 20px;
            text-align: center;
            margin-right: 10px;
            opacity: .6;
        }
        &:hover {
            background-color: rgba(0,0,0,.03);
            i {
                opacity: .8;
            }
        }
        &.active {
            color: rgba(0,0,0,.9);
            background-color: rgba(0,0,0,.03);
            &:before {
                background-color: #1a174d;
            }
            i {
                opacity: 1;
            }
        }
    }
    &-heading {
        margin: 0;
        padding: 10px 24px;
        font-size: .75rem;
        color: #8898aa;
        text-transform: uppercase;
    }
}



/*
* Content
*/
.sidebar ~ .main {
    padding-left: 290px; //sidebar width + 40
}
.main {
    padding: 10px 40px 40px;
}


/*
* page-header
*/
.page {
    &-header {
        position: relative;
        display: flex;
        justify-content: space-between;;
        padding-bottom: 15px;
        color: #fff;
    }
}

/*
 * heading
*/
h1 {
    font-size: 24px;
    margin: 0;
}
h2 {
    font-size: 20px;
    font-weight: bold;
    margin: 0;
}
h3 {
    font-size: 17px;
    font-weight: bold;
    margin: 0;
}
h4 {
    font-size: 15px;
    margin: 0;
}
h5 {
    font-size: 13px;
    margin: 0;
}
/*
* Card
*/
.card {
    border: 1px solid rgba(0,0,0,.05);
    box-shadow: 0 0 2rem 0 rgba(136,152,170,.15);
    &-header,
    &-footer {
        background-color: #fff;
    }
    &-body {
    }
    ~ .card {
        margin-top: 20px;
    }
}

/*
* table
*/
.table {
    color: #525f7f;
    th {
        padding: 10px 16px;
    }
    td {
        padding: 16px;
    }
    &-overflow {
        display: block;
        width: 100%;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        th,
        td {
            white-space: nowrap;
        }
    }
    &-horizontal {
        th {
            background-color: #000;
            color: #8898aa;
            border-color: #e9ecef;
            font-weight: normal;
        }
        td {
            .form-group {
                margin-bottom: 0;
            }
        }
    }
    .thead {
        &-light {
            th {
                background-color: #f6f9fc;
                color: #8898aa;
                border-bottom: 1px solid #e9ecef;
                font-size: 10px;
                text-transform: uppercase;
                font-weight: normal;
                letter-spacing: 1px;
            }
        }
    }
}



/*
* Pagination
*/
.pagination {
    margin: 0;
    .page-item {
        .page-link {
            color: #343a40;
        }
        &.disabled {
            .page-link {
                color: #ddd;
            }
        }
        &.active {
            .page-link {
                color: #fff;
                border-color: #343a40;
                background-color: #343a40;
            }
        }
    }
}

/*
* tab
*/
.nav-pills {
    .nav-link {
        color: #343a40;
        &.active {
            background-color: #343a40;
        }
    }
}

/*
* Badge
*/
.badge {
    text-transform: capitalize;
    font-size: 12px;
}

/*
* icon
*/
.icon {
    width: 48px;
    height: 48px;
    &-shape {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 12px;
        text-align: center;
    }
}

/*
 * form
*/
.form-input {
    display: inline-block;
    .input{
        display: inline-block;
        vertical-align: middle;
        position: relative;
        width: 17px;
        height: 17px;
   }
   label {
        display: inline-block;
        vertical-align: middle;
        margin: 0;
    }
    input[type="checkbox"],
    input[type="radio"] {
        position: absolute;
        left: 0;
        top: 0;
        width: 17px;
        height: 17px;
        opacity: 0;
        cursor: pointer;
        + i {
            display: inline-block;
            width: 17px;
            height: 17px;
            border: 1px solid #ddd;
            background-color: #fff;
            border-radius: 2px;
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                opacity: 0;
                transition: opacity .2s;
                pointer-events: none;
            }
        }
        &:checked {
            + i {
                background-color: #007bff;
                border-color: #007bff;
                &::before {
                    opacity: 1;
                }
            }
        }
    }
    input[type="checkbox"] {
        + i {
            &:before {
                width: 7px;
                height: 5px;
                margin: 5px;
                transform: rotate(-45deg);
                border-left: 2px solid #fff;
                border-bottom: 2px solid #fff;
            }
        }
    }
    input[type="radio"] {
        + i {
            border-radius: 100%;
            &:before {
                width: 7px;
                height: 7px;
                margin: 5px;
                background-color: #fff;
                border-radius: 100%;
            }
        }
    }
}

/*
 * btn
*/
.btn {
    &-icon {
        width: 30px;
        padding: 0;
        line-height: auto;
        font-size: 13px;
    }
}

@function text-contrast($bg-color, $val: 70%) {

    // 변수 $value 초기 값 설정 null
    $value: null;
    @debug "사용자가 전달한 값은 #{$bg-color}입니다.";
    // 전달인자 값을 확인 (유효성 검사)
    @if (type-of($bg-color) != color) {
      @warn "첫번째 전달인자 값은 컬러 값이어야 합니다. 예) #fff, red, rgba()";
      @debug "사용자가 전달한 값은 #{$bg-color}입니다.";
    }
    @if (lightness($bg-color) > 50%) {
      $value: darken($bg-color, $val)
    }
    @else {
      $value: lighten($bg-color, $val)
    }
    // 함수는 반드시 return 값이 필요하다
    @return $value;
}

@function lt_Spacing($spacing) {
    @if (type-of($spacing) !=number) {
      @warn "전달한 인자 값은 #{$spacing}입니다 제대로 된 값(숫자)을 전달해주세요.";
    }
    @return deUnit($spacing) / 1000+em;
}

@function deUnit($value) {
    @return ($value / ($value * 0 + 1));
}

.demo-01 {
    background-color: text-contrast(#c4c4c4, 50%)
}

.demo-02 {
    letter-spacing: lt_Spacing(16);
}
